<template>
    <div class="box-content">
        <div class="content" v-show="!nullData">
            <div class="item" v-for="item in arr" :key="item.title + item.fileId">
                <img v-lazy="item.imgUrl"/>
                <div class="info">
                    <router-link :to="{ path:'/show', query:{ page:item.subTitle == null ? 'essays' : 'technology',id:item.fileId,title:item.realTitle } }" class="title"><span v-html="item.title"></span></router-link>
                    <span class="date">{{ item.subTitle == null ? "杂文" : "技术" }} | {{ item.date }}</span>
                </div>
            </div>
        </div>
        <div class="tip" v-show="error">发生未知错误...</div>
        <div class="tip" v-show="loading   && !nullData">数据加载中...</div>
        <div class="tip" v-show="loadedAll && !nullData">已加载完成所有数据...</div>
        <div class="tip" v-show="nullData">
            <br/><img src="@/assets/img/null-data.svg" class="none-img"/><br/><br/>
            检索不到与“ <font class='none'>{{ key }}</font> ”相关的数据~
        </div>
    </div>
</template>

<script>
import eventBus from '../utils/eventBus'
export default {
    name: "Search",
    data(){
        return {
            error:false,
            loading: true,
            loadedAll: false,
            nullData:false,
            arr:[],
            data:[],
            dataSize:0,
            loadedDataSize:0,
            pageItemSize:10,
            timer:800
        }
    },
    created:function(){
        this.setData()
    },
    mounted(){
         eventBus.$on("search",() => {
             this.loadedAll = false
             this.nullData  = false
             this.error     = false
             this.data.length    = 0
             this.arr.length     = 0
             this.dataSize       = 0
             this.loadedDataSize = 0
             this.setData()
         })

         this.$on("scrollBottom",() => {
             this.loadData()
         })
    },
    computed:{
        key(){
            return this.$store.state.global.searchKey
        }
    },
    methods:{
        setData:function(){
            let panter = new RegExp(this.key.toLowerCase())
            let tJsonData = JSON.parse(JSON.stringify(this.$store.state.technology.data))
            let eJsonData = JSON.parse(JSON.stringify(this.$store.state.essays.data))
            this.filterData(this.data,tJsonData,panter,this.key)
            this.filterData(this.data,eJsonData,panter,this.key)
            this.dataSize = this.data.length
            if(this.dataSize == 0){
                this.loading  = false
                this.nullData = true
            }
            else{
                this.loadData()
            }
        },
        filterData:function(data,json,panter,key){
            json.forEach(item => {
                let i = item
                if(panter.test(i.title.toLowerCase())){
                    i.realTitle = i.title
                    i.title = i.title.toLowerCase().replace(panter,'<font color="red">' + key + '</font>')
                    data.push(i)
                }
            })
        },
        loadData:function(){
            if(this.key == ""){
                this.error   = true
                this.loading = false
                return
            }
            if(this.loadedAll)
                return
                
            this.loading = true
            setTimeout(() => {
                if(this.loadedDataSize < this.dataSize){
                    let differ = this.dataSize - this.loadedDataSize
                    differ = differ < this.pageItemSize ? differ : this.pageItemSize
                    for(let i = this.loadedDataSize; i < this.loadedDataSize + differ; i++){
                        this.arr.push(this.data[i])
                    }
                    this.loadedDataSize += differ
                }
                this.loading = false
                if(this.loadedDataSize == this.dataSize){
                    this.loadedAll = true
                }
            },this.timer)
        }
    }
}
</script>


<style scoped>
    @media screen and (min-width:666px) {
        .item{
            width: 80%;
        }
        .item img{
            border-radius: 5%;
        }
        .date{
            font-size: 1rem;
            margin-right: 15px;
        }
        .none-img{
            width: 20%;
        }
    }
    @media screen and (max-width:666px) {
        .item{
            width: 80%;
        }
        .item img{
            border-radius: 15%;
        }
        .date{
            font-size: 0.5rem;
            margin-right: 10px;
        }
        .none-img{
            width: 50%;
        }
    }
    .item:first-child{
        margin-top: 25px;
    }

    .item{
        height: 100px;
        margin: 0 auto;
        margin-bottom: 35px;
    }

    .item img{
        width: 25%;
        height: 100px;
        object-fit: cover;
        float: left;
        opacity: 0.5;
        transition: opacity 0.3s ease-in-out;
    }

    .info{
        width: 85%;
        height: 100px;
        margin-left: 20%;
        background: cornsilk;
        box-shadow: 0px 0 2px #dadada;
    }

    .info span{
        margin-left: 25px;
    }

    .title{
        width: 70%;
        height: 70px;
        line-height: 90px;
        display: inline-block;
        font-size: 1.7rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: black;
        text-decoration: none;
    }

    .title:hover{
        text-decoration: underline;
    }
    .date{
        height: 30px;
        line-height: 10px;
        text-align: right;
        display: block;
    }

    .none{
        color: red;
        font-size: 1.5rem;
    }

</style>


