<template>
    <div class="box-markdown">
        <div class="tip" v-show="loading">加载中，请稍等...</div>
        <span class="title" v-show="!loading">{{ title }}</span>
        <div v-html="html" v-show="!loading"></div>
        <div class="md-footer" v-show="!loading">
            <span @click="$router.back()" class="btn-back"><img src="@/assets/img/back.svg"/>返回</span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Show',
    data(){
        return{
            loading:true,
            title:'',
            html:'',
            root:'https://md.jqwong.cn/'
        }
    },
    created:function(){
        this.title = this.$route.query.title
        this.load(this.$route.query.page,this.$route.query.id)
    },
    methods:{
        load:function(page,id){
            let path = this.root + page + "/" + id + ".md"
            this.$axios.get(path)
            .then(res => {
                let md = res.data
                md = this.$marked(md,{ sanitize:true })
                console.log(md)
                md = md.replace(/&lt;div align=&#39;center&#39;&gt;/g,"<div align='center'>")
                md = md.replace(/&lt;div align=&amp;#39;center&amp;#39;&gt;/g,"<div align='center'>")
                md = md.replace(/<p>&lt;\/div&gt;/g,"<p></div>")
                md = md.replace(/&lt;br\/&gt;/g,"<br/>")
                md = md.replace(/\\/g,"")
                this.html = md
                this.loading = false
            })
            .catch(error => {
                this.html = "出现了未知错误.."
                this.loading = false
            })
        }
    }
}
</script>

<style>
@import url("../assets/css/md.css");
</style>

<style scoped>
@media screen and (min-width:666px) {
    .box-markdown{
        width: 50%;
    }
    .title{
        font-size: 3rem;
    }
    .title:before{
        width: 30%;
        left: 35%;
    }
    .title:after{
        width: 20%;
        left: 40%;
    }
}

@media screen and (max-width:666px){
    .box-markdown{
        width: 80%;
    }
    .title{
        font-size: 2rem;
    }
    .title:before{
        width: 60%;
        left: 20%;
    }
    .title:after{
        width: 40%;
        left: 30%;
    }
}

.title{
    width: 100%;
    display: block;
    text-align: center;
    position: relative;
    padding: 10px 0 10px 0;
    font-family:Georgia, 'Times New Roman', Times, serif;
    margin: 25px 0 5px 0;
}

.title:before{
    content: '';
    height: 1px;
    background: #f1f1f1;
    position: absolute;
    top: 0;
}
.title:after{
    content: '';
    height: 1px;
    background: #f1f1f1;
    position: absolute;
    bottom: 0;
}

</style>



