<template>
    <div class="box-content">
        <span class="tag-title" ref="tagTitle" @click="changeNavHeight(true,true)">{{ tagTitle }}<img ref="arrow" src="@/assets/img/arrow-r.svg" class="arrow"/></span>
        <nav class="menu-tag" ref="menuTag">
            <ul>
                <li :style="{'height':liHeight+'px','line-height':liHeight+'px'}" v-for="item in tags" :key="item.id" @click="load(item.id)" ref="tag" :id="item.id">{{ item.tag }}</li>
            </ul>
        </nav>
        <div class="md" v-html="html"></div>
        <div class="md-footer">
            <span @click="$router.back()" class="btn-back"><img src="@/assets/img/back.svg"/>返回</span>
        </div>
    </div>
</template>

<script>
export default {
    name:"Question",
    data(){
        return{
            loadedTag:false,
            loading:"数据加载中...",
            html:"",
            root:"https://md.jqwong.cn"
        }
    },
    created:function(){
        if(this.tags.length == 0){
            this.$parent.getData("question.json",data => {
                this.$store.commit('setQuestionTags',data)
            })
        }
    },
    mounted(){
        if(this.$refs.tag != null){
            this.load(this.selectId)
        }
    },
    updated(){
        if(this.$refs.tag != null && this.loadedTag == false){
            this.load(this.selectId)
            this.loadedTag = true
        }
    },
    computed:{
        tags(){
            return this.$store.state.question.tags
        },
        htmls(){
            return this.$store.state.question.htmls
        },
        selectId(){
            return this.$store.state.question.selectId
        },
        tagTitle(){
            return this.$store.state.question.tagTitle
        },
        liHeight(){
            return this.$store.state.question.liHeight
        },
        navHeight(){
            return this.$store.state.question.navHeight
        }
    },
    methods:{
        load(id){
            this.changeClass(id)
            this.changeTitle(id)
            this.changeNavHeight(false,false)
            this.html = this.loading
            this.$store.commit("setQuestionSelectId",id == null ? this.selectId : id)
            let html = this.getHTML()
            if(html == ""){
                let url = this.root +"/question/" + this.selectId + ".md"
                this.$axios.get(url).then(res => {
                    let md = this.$marked(res.data,{ sanitize:true })
                    md = md.replace(/&lt;br\/&gt;/g,"<br/>")
                    this.html = md
                    let obj = {
                        "id":this.selectId,
                        "html":this.html
                    }
                    this.$store.commit("setQuestionHTML",obj)
                })
            }
            else{
                this.html = html
            }
        },
        getHTML(){
            let html = ""
            if(this.htmls.length != 0){
                this.htmls.forEach(item => {
                    if(item.id == this.selectId){
                        html =  item.html
                    }
                })
            }
            return html
        },
        changeClass(id){
            let ref = this.$refs.tag
            ref.forEach(el => {
                switch(parseInt(el.id)){
                    case id:
                        el.className = "select"
                        break;
                    case this.selectId:
                        el.className = ""
                        break;
                }
            })
        },
        changeTitle(id){
            this.tags.forEach(item => {
                if(item.id == id){
                    this.$store.commit("setQuestionTagTitle",item.tag)
                    return
                }
            })
        },
        changeNavHeight(flag,isTitle){
            let height = parseInt(this.$refs.menuTag.style.height)
            if(isTitle && height != 0){
                height = 0    
            }
            else{
                height = flag ? this.navHeight : 0
            }
            this.$refs.menuTag.style.height = height + "px"
            let deg =  height == 0 ? 0 : 90
            this.$refs.arrow.style.transform = "rotate(" + deg + "deg)" 
        }
    }
}
</script>

<style>
    @import url("../assets/css/md.css");
    @media screen and (min-width:640px) {

        .tag-title{
            display: none;
        }

        .arrow{
            display: none;
        }

        .menu-tag{
            width: 30%;
            float: left;
            text-align: center;
            margin-top: 25px;
        }

        .menu-tag li{
            width: 35%;
            margin: 0 auto;
            margin-top: 25px;
            border: 3px solid salmon;
            cursor: pointer;
        }

        .md{
            float: left;
            width: 70%;
        }

        .select{
            background: salmon;
            color:white;
            border-radius: 5px;
        }
    }

    @media screen and (max-width:640px) {
        .tag-title{
            width: 80%;
            text-align: center;
            font-size: 3rem;
            /*font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;*/
            /*font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;*/
            font-family:cursive;
            display: block;
            position: relative;
            margin: 0 auto;
            margin-bottom: 10px;
            margin-top: 25px;
            position: relative;
        }
        .tag-title:before{
            content: '';
            width: 60%;
            height: 1px;
            background: #f1f1f1;
            position: absolute;
            top: 0;
            left: 20%;
        }
        .tag-title:after{
            content: '';
            width: 40%;
            height: 1px;
            background: #f1f1f1;
            position: absolute;
            bottom: 0;
            left: 30%;
        }
        .arrow{
            width: 20px;
            position: absolute;
            top: 45%;
            right: 0;
            transition: transform 0.1s ease-in-out;
        }
        .menu-tag{
            width: 80%;
            text-align: center;
            background: salmon;
            transition: height 0.1s ease-in-out;
            margin: 0 auto;
            overflow: hidden;
        }
        .menu-tag ul{
            padding: 0;
            margin: 0;
        }
        .menu-tag li{
            background: wheat;
        }
    }
    .box-content{
        width: 80%;
    }

    .menu-tag li{
        list-style: none;
        transition: all 0.1s ease-in-out;
    }
</style>


