import Vue  from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const moduleTechnology = {
    state:{
        arr:[],
        data:[],
        pageItemSize:10,
        dataSize:0,
        loadedDataSize:0,
        loading:true,
        loadedAll:false,
        timer:800
    },
    mutations:{
        setTechnologyData(state,data){
            state.data = data
            state.dataSize = data.length
        },
        setTechnologyLoading(state,flag){
            state.loading = flag
        },
        setTechnologyLoadedAll(state){
            state.loadedAll = true
        },
        addTechnologyArrData(state,differ){
            let start = state.loadedDataSize
            for(let i = start; i < start + differ; i++){
                state.arr.push(state.data[i])
            }
            state.loadedDataSize += differ
        }
    }
}

const moduleQuestion = {
    state:{
        tags:[],
        htmls:[],
        tagTitle:"",
        selectId:1,
        liHeight:40,
        navHeight:0
    },
    mutations:{
        setQuestionTags(state,data){
            state.tags = data
            this.commit("setQuestionNavHeight")
        },
        setQuestionTagTitle(state,title){
            state.tagTitle = title
        },
        setQuestionSelectId(state,id){
            state.selectId = id
        },
        setQuestionHTML(state,obj){
            state.htmls.push(obj)
        },
        setQuestionNavHeight(state){
            state.navHeight = state.tags.length * state.liHeight
        }
    }
}

const moduleEssays = {
    state:{
        arr:[],
        data:[],
        dataSize:0,
        loadedDataSize:0,
        loading:true,
        loadedAll:false,
        pageItemSize:10,
        timer:800
    },
    mutations:{
        setEssaysData(state,data){
            state.data = data
            state.dataSize = data.length
        },
        setEssaysLoading(state,flag){
            state.loading = flag
        },
        setEssaysLoadedAll(state){
            state.loadedAll = true
        },
        addEssaysArrData(state,differ){
            let start = state.loadedDataSize
            for(let i = start; i < start + differ; i++){
                state.arr.push(state.data[i])
            }
            state.loadedDataSize += differ
        }
    }
}

const moduleGlobal = {
    state:{
        searchKey:""
    },
    mutations:{
        setSearchKey(state,key){
            state.searchKey = key
        }
    }
}

export default new Vuex.Store({
    modules:{
        technology:moduleTechnology,
        question:moduleQuestion,
        essays:moduleEssays,
        global:moduleGlobal
    }
})


