<template>
    <div class="box-content">
        <div class="content">
            <ul>
                <li v-for="item in arr" :key="item.fileId">
                    <div class="item flex-column">
                        <router-link :to="{ path:'/show', query:{ page:'technology',id:item.fileId,title:item.title } }" class="title">{{item.title}}</router-link>
                        <span class="sub-title">{{item.subTitle}}</span>
                        <img v-lazy="item.imgUrl"/>
                        <div class="info">
                            <span class="tag" v-for="tag in item.tag" :key="tag">{{ tag }}</span>
                            <span class="date">{{ item.date }}</span>
                        </div>
                        <span class="hr"></span>
                    </div>
                </li>
            </ul>
        </div>
        <div class="tip" v-show="loading">加载数据中...</div>
        <div class="tip" v-show="loadedAll">已加载完成所有数据...</div>
    </div>
</template>

<script>
import eventBus from '../utils/eventBus'
export default {
    name:"Technology",
    methods:{
        loadData:function(){
            if(this.loadedAll)
                return
                
            this.$store.commit('setTechnologyLoading',true)
            setTimeout(() => {
                if(this.loadedDataSize < this.dataSize){
                    let differ = this.dataSize - this.loadedDataSize
                    differ = differ < this.pageItemSize ? differ : this.pageItemSize
                    this.$store.commit('addTechnologyArrData',differ)
                }
                this.$store.commit('setTechnologyLoading',false)
                if(this.loadedDataSize == this.dataSize){
                    this.$store.commit('setTechnologyLoadedAll')
                }
            },this.timer)
        }
    },
    created:function(){
        this.$store.commit("setSearchKey","")
        if(this.data.length == 0){
            this.$parent.getData("technology.json",(data)=>{
                this.$store.commit('setTechnologyData',data)
                this.loadData()
            })
        }
    },
    destroyed(){
        eventBus.$off('search')
    },
    mounted:function(){
        this.$on("scrollBottom",() => {
            this.loadData()
        })
    },
    computed:{
        data(){
            return this.$store.state.technology.data
        },
        dataSize(){
            return this.$store.state.technology.dataSize
        },
        loadedDataSize(){
            return this.$store.state.technology.loadedDataSize
        },
        arr(){
            return this.$store.state.technology.arr
        },
        loading(){
            return this.$store.state.technology.loading
        },
        loadedAll(){
            return this.$store.state.technology.loadedAll
        },
        pageItemSize(){
            return this.$store.state.technology.pageItemSize
        },
        timer(){
            return this.$store.state.technology.timer
        }
    }
}
</script>

<style scoped>
    @media screen and (max-width:640px) {

        .info{
            text-align: center;
            padding: 15px 0 8px 0;
        }

        .date{
            width: 100%;
            text-align: center;
            position: relative;
            display: block;
            margin-top: 10px;
            font-size: 0.8rem;
        }

        .item>img{
            width: 100%;
            height: 250px;
        }

    }

    @media screen and (min-width:640px) {

        .date{
            position: absolute;
            right: 15px;
            font-size: 1rem;
        }
        
        .tag:first-child{
            margin-left: 25px;
        }
        
        .info{
            width: 90%;
            padding: 15px 0 15px 0;
        }

        .item>img{
            width: 100%;
            height: 400px;
        }
    }
    
    .content ul{
        padding: 0;
        margin: 0;
    }

    .content li{
        list-style: none;
    }

    .item{
        width: 100%;
        margin-bottom: 35px;
    }

    .item span{
        display: block;
    }

    .item>.title{
        width: 80%;
        text-align: center;
        font-size: 2.5rem;
        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        color: black;
        text-decoration: none;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .item>.title:hover{
        text-decoration: underline;
    }

    .item>.sub-title{
        text-align: center;
        font-size: 0.9rem;
        margin-bottom: 5px;
    }

    .item>img{
        border-radius: 25px;
        object-fit: cover;
        box-shadow: 1px 3px 15px #dadada;
        opacity: 0.5;
        transition: opacity 0.3s ease-in-out;
    }

    .info{
        position: relative;
    }

    .info>span{
        display: inline-block;
    }

    .info>.tag{
        background: #dadada;
        padding: 2px 5px 2px 5px;
        margin-right: 7px;
    }

    .hr{
        display: block;
        width: 50%;
        height: 1px;
        background: #dadada;
    }
</style>


