<template>
    <div class="hHeader flex-column">
        <span class="hTag">Jqwong's Blog</span>
        <nav class="hMenu">
            <ul>
                <li><router-link to="/" exact>主页</router-link></li>
                <li><router-link to="/technology">技术</router-link></li>
                <li><router-link to="/essays">杂文</router-link></li>
                <li><router-link to="/question">问题</router-link></li>
            </ul>
        </nav>
        <div class="box-search" v-show="this.$route.path != '/' && this.$route.path != '/question'">
            <input type="text" class="input-search" placeholder="使用关键字搜索.." :value="searchKey" ref="inputSearch"/>
            <img src="@/assets/img/search.svg" class="img-search" @click="goSearch"/>
        </div>
    </div>
</template>

<script>
import eventBus from '../utils/eventBus'
export default {
  name: 'Header',
  computed:{
      searchKey(){
          return this.$store.state.global.searchKey
      }
  },
  methods:{
      goSearch:function(){
        let v = this.$refs.inputSearch.value
        if(v == "" || v == this.searchKey)
            return
        this.$store.commit("setSearchKey",v)
        if(this.$route.path == "/search"){
            eventBus.$emit("search")
        }
        else{
            this.$router.push({ path:"/search", query:{ key:v } })
        }
      }
  }
}
</script>

<style scoped>
  @media screen and (max-width:1920px){
    .hTag{
        font-size: 5em;
    }
    .hMenu li{
        margin-top: 20px !important;
    }
    .hMenu a{
        font-size: 1.8rem;
    }
  }

  @media screen and (max-width:1366px){
    .hTag{
        font-size: 3em;
    }
    .hMenu a{
        font-size: 1.3rem;
    }
  }

  @media screen and (max-width:640px){
      
  }
    .hHeader{
        width: 100%;
        margin-top: 25px;
    }
    .hTag{
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        font-weight: bolder;
    }

    

    .hMenu li{
        float: left;
        list-style: none;
        padding-bottom: 5px;
        margin: 10px;
        font-weight: 100;
        position: relative;
    }

    .hMenu a{
        color: black;
        text-decoration: none;
        font-family: 'MingLiU';
        transition: color 0.2s ease-in-out;
    }

    .hMenu li:hover a{
        color: salmon;
    }

    .hMenu a::after{
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background: salmon;
        bottom: 0;
        left: 0;
        transform: scaleX(0);
        transform-origin: center center;
        transition: transform 0.2s ease-in-out;
    }

    .hMenu a:hover:after{
        transform: scaleX(1);
    }

    a.router-link-exact-active{
        color: salmon;
    }

    a.router-link-exact-active::after{
        transform: scaleX(1);
    }

    .box-search{
        width: 250px;
        position: relative;
        margin: 15px 0 25px 0;
    }

    .input-search{
        width: 200px;
        padding: 10px 45px 10px 5px;
        outline: none;
        border: 3px solid #dadada;
        border-radius: 5px;
        transition: border 0.2s ease-in-out;
    }

    .input-search:focus{
        border: 3px solid salmon;
    }

    .img-search{
        position: absolute;
        right: 5px;
        top: 8px;
        cursor: pointer;
    }
</style>


