<template>
  <div id="app" class="flex-column">
    <Header></Header>
    <router-view ref="child"/>
    <span class="btn-top" ref="btnTop" @click="toTop"><img src="@/assets/img/top.svg"/></span>
  </div>
</template>

<script>
import Header from './components/Header'
export default {
  name: 'App',
  components:{
    Header
  },
  data(){
    return {
      timer:1000,
      timesStamp:0,
      heightPoint:0
    }
  },
  mounted:function(){
    let _this = this
    _this.heightPoint = (document.documentElement.scrollHeight + 100)
    _this.$nextTick(() => {
      window.addEventListener("scroll",_this.handlerScroll)
    })
  },
  methods:{
    handlerScroll:function(){
      let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      let differ    = Math.abs(document.documentElement.scrollHeight - (document.documentElement.clientHeight + parseInt(scrollTop)))
      
      // 到达底部
      if(differ >= 0 && differ <= 5) {
        let now = (new Date()).getTime()
        
        if(now - this.timesStamp < this.timer)
          return
        this.timesStamp = now
        this.$refs.child.$emit("scrollBottom")
      }
      // 滑动按钮
      if(scrollTop > this.heightPoint){
        this.$refs.btnTop.className = "btn-top ani-in"
      }
      else{
        let className = this.$refs.btnTop.className
        this.$refs.btnTop.className = className == "btn-top" ? "btn-top" : "btn-top ani-out"
      }
    },
    toTop:function(){
      document.body.scrollTop=document.documentElement.scrollTop=0
    },
    getData(fileName,call){
        this.$axios.get("./static/data/"+fileName + "?r="+Math.random())
        .then(res => {
          call(res.data.data)
        })
    }
  }
}
</script>

<style>
  @media screen and (max-width:1920px){
      .content{
        width: 50%;
      }
  }

  @media screen and (max-width:1366px){
      .content{
          width: 60%;
      }
  }

  @media screen and (max-width:640px){
      .content{
          width: 95%;
      }
  }
  
  
  *{
    padding: 0;
    margin: 0;
  }

  html,body,#app{
    width: 100%;
  }

  .flex-row{
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .flex-column{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .box-content{
    width: 80%;
    margin: 0 auto;
  }
  
  .content{
      margin: 0 auto;
      height: auto;
  }

  .tip{
    text-align: center;
    margin-bottom: 25px;
  }

  .btn-top{
    width: 45px;
    height: 45px;
    border-radius: 45px;
    position: fixed;
    bottom: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: salmon;
    cursor: pointer;
    box-shadow: 0 0 3px slategray;
    right: -60px;
  }

  .btn-top img{
    width: 25px;
    height: 25px;
  }

  .md-footer{
      text-align: right;
  }
  .btn-back{
      padding: 15px 0 15px 0;
      cursor: pointer;
      position: relative;
  }

  .btn-back>img{
      position: absolute;
      width: 25px;
      opacity: 0;
      transition: all 0.2s ease-in-out;
      right: 25px;
  }

  .btn-back:hover img{
      opacity: 1;
      right: 35px;
  }


  .ani-in{
    animation: in 0.2s ease-in-out forwards;
  }

  .ani-out{
    animation: out 0.2s ease-in-out forwards;
  }

  @keyframes in{
    from{
      right: -60px;
    }
    to{
      right: 30px;
    }
  }

  @keyframes out{
    from{
      right: 30px;
    }
    to{
      right: -60px;
    }
  }
</style>



