<template>
    <div class="box-content">
        <div class="content">
            <ul>
                <li class="item" v-for="item in arr" :key="item.fileId" v-lazy:background-image="item.imgUrl">
                    <router-link :to="{ path:'/show', query:{ page:'essays',id:item.fileId,title:item.title } }">{{ item.title }}</router-link>
                </li>
            </ul>
        </div>
        <div class="tip" v-show="loading">数据加载中...</div>
        <div class="tip" v-show="loadedAll">已加载完成所有数据...</div>
    </div>
</template>

<script>
import eventBus from '../utils/eventBus'
export default {
    name:"Essays",
    computed:{
        arr(){
            return this.$store.state.essays.arr
        },
        data(){
            return this.$store.state.essays.data
        },
        dataSize(){
            return this.$store.state.essays.dataSize
        },
        loadedDataSize(){
            return this.$store.state.essays.loadedDataSize
        },
        pageItemSize(){
            return this.$store.state.essays.pageItemSize
        },
        loading(){
            return this.$store.state.essays.loading
        },
        loadedAll(){
            return this.$store.state.essays.loadedAll
        },
        timer(){
            return this.$store.state.essays.timer
        }
    },
    created:function(){
        this.$store.commit("setSearchKey","")
        if(this.data.length == 0){
            this.$parent.getData("essays.json",data => {
                this.$store.commit("setEssaysData",data)
            })
        }
    },
    destroyed(){
        eventBus.$off('search');
    },
    mounted:function(){
        this.loadData()
        this.$on("scrollBottom",() => {
            this.loadData()
        })
    },
    methods:{
        loadData:function(){
            if(this.loadedAll)
                return
            
            this.$store.commit('setEssaysLoading',true)
            setTimeout(() => {
                if(this.loadedDataSize < this.dataSize){
                    let differ = this.dataSize - this.loadedDataSize
                    differ = differ < this.pageItemSize ? differ : this.pageItemSize
                    this.$store.commit('addEssaysArrData',differ)
                }
                this.$store.commit('setEssaysLoading',false)
                if(this.loadedDataSize == this.dataSize){
                    this.$store.commit('setEssaysLoadedAll')
                }
            },this.timer)
        }
    }
}
</script>

<style scoped>
    
    .content ul{
        padding: 0;
        margin: 0;
    }
    .content li{
        list-style: none;
        margin-bottom: 25px;
    }
    .content li:first-child{
        margin-top: 0 !important;
    }
    .item{
        width: 100%;
        height: 100px;
        margin: 0 auto;
        line-height: 100px;
        text-align: center;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-size:cover;
        box-shadow: 2px 3px 12px #dadada;
        margin-top: 45px;
        opacity: 0.5;
        transition: opacity 0.3s ease-in-out;
    }

    .item>a{
        color: white;
        font-size: 2rem;
        text-decoration: none;
    }

    .item>a:hover{
        text-decoration: underline;
    }
</style>


